<template>
    <div>
        <div data-v-4553c0c1="" data-v-7d39b64d="" class="cart-main">

            <div data-v-152ab910="" data-v-4553c0c1="" class="sub-header">
                <div data-v-152ab910="" class="back" @click="$router.go(-1)"
                     v-show="$route.query.from==='goods_details'?true:false"></div>
                <div data-v-152ab910="" class="title">购物车</div><!----></div>
            <div data-v-4553c0c1="" class="cart-list" v-for="(item,index) in goodsDatas" :key="index">
                <div data-v-4553c0c1="" :class="{'select-btn':true, active:item.checked}"
                     @click="selectGoodsDatas(index)"></div>
                <div data-v-4553c0c1="" class="image-wrap">
                    <div data-v-4553c0c1="" class="image">
                        <img :src="item.img"/>
                    </div>
                    <div data-v-4553c0c1="" class="del" @click="DEL_GOODSDATAS({index:index})">删除</div>
                </div>
                <div data-v-4553c0c1="" class="goods-wrap">
                    <div data-v-4553c0c1="" class="goods-title">{{item.title}}</div>
                    <div data-v-4553c0c1="" class="goods-attr">
                          <span v-for="(item2,index2) in item.attrs" :key="index2">{{item2.title}}：
                        <template v-for="(item3,index3) in item2.param">{{item3.title}}</template>
                    </span>
                    </div>
                    <div data-v-4553c0c1="" class="buy-wrap">
                        <div data-v-4553c0c1="" class="price">¥{{item.price}}</div>
                        <div data-v-4553c0c1="" class="amount-input-wrap">
                            <div data-v-4553c0c1="" :class="item.amount>1?'btn dec':'btn dec active'" @click="DEC_GOODSDATAS({index:index})">-</div>
                            <div data-v-4553c0c1="" class="amount-input">
                                <input data-v-4553c0c1="" @input="CHANGE_GOODSDATAS({index:index,amount:$event.target.value})" type="number" :value="item.amount"/>
                            </div>
                            <div data-v-4553c0c1="" class="btn inc" @click="ADD_GOODSDATAS({index:index})">+</div>
                        </div>
                    </div>
                </div>
            </div>
            <div data-v-4553c0c1="" class="orderend-wrap">
                <div data-v-4553c0c1="" class="select-area">
                    <div data-v-4553c0c1="" class="select-wrap" @click="onAllSelectGoods()">
                        <div  :class="{'select-btn':true, active:isAllGoodsdatas}"></div>
                        <div data-v-4553c0c1="" class="select-text">全选</div>
                    </div>
                    <div data-v-4553c0c1="" class="total">运费：<span data-v-4553c0c1="">¥{{freight}}</span> 合计：<span
                            data-v-4553c0c1="">¥{{total}}</span></div>
                </div>
                <div data-v-4553c0c1="" :class="total>0?'orderend-btn':'orderend-btn disable'" @click="goOrderPage">去结算</div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState, mapMutations,mapGetters} from "vuex"

    export default {
        data(){
            return {
                //全选/反选
                isAllGoodsdatas:true
            }
        },
        created(){
            this.checkIfSelect();
        },
        computed: {
            ...mapState({
                //购物车数据
                goodsDatas: state => state.cart.goodsDatas
            }),
            ...mapGetters({
                //总价格
                total:"cart/total",
                //运费
                freight:"cart/freight"
            })
        },
        methods: {
            ...mapMutations({
                //删除商品
                DEL_GOODSDATAS:"cart/DEL_GOODSDATAS",
                //增加商品数量
                ADD_GOODSDATAS:"cart/ADD_GOODSDATAS",
                //减少商品数量
                DEC_GOODSDATAS:"cart/DEC_GOODSDATAS",
                //选中商品
                SELECT_GOODSDATAS:"cart/SELECT_GOODSDATAS",
                //更改商品数量
                CHANGE_GOODSDATAS:"cart/CHANGE_GOODSDATAS",
                //全选/反选
                ALL_CHANGE_GOODSDATAS:"cart/ALL_CHANGE_GOODSDATAS"
            }),
            //选中商品
            selectGoodsDatas(index){
                this.SELECT_GOODSDATAS({index:index});
                if (this.goodsDatas.length>0){
                    let isALLStyle = true;
                    for (let i = 0;i<this.goodsDatas.length;i++){
                        if (!this.goodsDatas[i].checked){
                            isALLStyle = false;
                            break;
                        }
                    }
                    this.isAllGoodsdatas = isALLStyle;
                }else {
                    this.isAllGoodsdatas= false;
                }

            },
            //全选/反选
            onAllSelectGoods(){
               if (this.goodsDatas.length>0){
                   this.isAllGoodsdatas = !this.isAllGoodsdatas;
                   this.ALL_CHANGE_GOODSDATAS({checked:this.isAllGoodsdatas})
               }
            },
            //检测是否选择
            checkIfSelect(){
                if (this.goodsDatas.length>0){
                    let isALLStyle = true;
                    for (let i = 0;i<this.goodsDatas.length;i++){
                        if (!this.goodsDatas[i].checked){
                            isALLStyle = false;
                            break;
                        }
                    }
                    this.isAllGoodsdatas = isALLStyle;
                }else {
                    this.isAllGoodsdatas= false;
                }
            },
            //结算 去订单页面
            goOrderPage(){
                if(this.total>0){
                    this.$router.push('/order')
                }
            }
        }
    }
</script>
